import { setPayPassWord , getCodeByPhone } from '@/utils/ApiList.js'
import { mapState } from 'vuex'
import baseUrl from '@/utils/url.js'
const app =getApp()
export default {
    data() {
        return {
            bg:this.imgs.loginBg,
            account: '',
            passwordF:'',
            passwordS: '',
            phone: '',
            smsCode:'',
            cntNum:119,
            isShowNumber: false,
            timer:0,
			cardId: app.globalData.cardId									//卡号
            // open_id: window.localStorage.getItem('OPEN_ID') || ''       //openid
        }
    },
    mounted () {
        clearInterval(this.timer)
        this.timer=0;
    },
    methods: {
        setPassword: function() {
            let that = this
            if (that.cardId=='') {
                uni.navigateTo({url:'/pages/change'})
            }else {
                if (that.smsCode ==''){
					uni.showToast({
						title:'请输入验证码',
						icon:'none'
					})
                }else {
					uni.request({
						url: baseUrl+'wx/set_pay_password',
						data: {
							card_no: that.cardId,
							pay_password: that.passwordF,
							phone_number: that.phone,
							sms_code: that.smsCode
						},
						method:'POST',
						success:res=>{
							console.log(res)
							if (res.statusCode == 200) {
								uni.showToast({
									title: res.data.msg,
									icon:'none'
								})
								let t=setTimeout(()=>{
									uni.navigateBack()
									clearTimeout(t)
								},1000)
							}else {
								uni.showToast({
									title: res.errMsg,
									icon:'none'
								})
							}
						},
					})
      //               setPayPassWord({
      //                   card_no: that.cardId,
      //                   pay_password: that.passwordF,
      //                   phone_number: that.phone,
      //                   sms_code: that.smsCode,
      //                   open_id: that.open_id
      //               }).then(res => {
						// console.log(res)
      //                   if (res.statusCode == 200) {
      //                       // that.$alert(res.data.msg, '提示', {
      //                       //     confirmButtonText: '确定',
      //                       //     callback: action => {
      //                       //         that.$router.go(-1)
      //                       //     }
      //                       // })
						// 	uni.showToast({
						// 		title: res.data.msg,
						// 		icon:'none',
						// 		complete: action=>{
						// 			uni.navigateBack()
						// 		}
						// 	})
      //                   }else {
						// 	uni.showToast({
						// 		title: res.errMsg,
						// 		icon:'none'
						// 	})
      //                   }
      //               }).catch(err => {
						// uni.showToast({
						// 	title: err.errMsg,
						// 	icon:'none'
						// })
      //               })
                }
               
            }
        },
        checkPhone:function () {
            if (!(/^1[345678]\d{9}$/).test(this.phone)){
				uni.showToast({
					title:'手机号格式不正确',
					icon:'none'
				})
            }
        },
        getSmsCode: function () {
            if (this.phone=='') {
				uni.showToast({
					title:'请输入手机号',
					icon:'none'
				})
            }else {
                getCodeByPhone({
                    phone_number:this.phone
                }).then(res => {
                        console.log(res)
                    if (res.data.code == 0) {
                        uni.showToast({
							title:'验证码发送成功',
							icon:'none'
						})
                        this.isShowNumber = true
                        this.timer = setInterval(this.cntNumDown,1000)
                    }
                })
            }
        },
        cntNumDown:function () {
            if (this.cntNum>0){
                this.cntNum-=1
            }else {
                this.cntNum=119 
                this.isShowNumber=false
                clearInterval(this.timer)
                this.timer = 0
            }            
        },
        check: function () {
            if (this.passwordS != this.passwordF) {
                // this.$alert('两次输入不一致，请重新输入', '提示', {
                //     confirmButtonText: '确定',
                // });
				uni.showToast({
					title:'两次输入不一致，请重新输入',
					icon:'none'
				})
            }
        }
    },
}
